<template>
  {{{content}}}
</template>

<style lang="styl">
  .loader,
  .loader:before,
  .loader:after
    background: #ffffff
    -webkit-animation: load1 1s infinite ease-in-out
    animation: load1 1s infinite ease-in-out
    width: 1em
    height: 4em

  .loader:before,
  .loader:after
    position: absolute
    top: 0
    content: ''

  .loader:before
    left: -1.5em
    -webkit-animation-delay: -0.32s
    animation-delay: -0.32s

  .loader
    color: #444
    text-indent: -9999em
    margin: 88px auto
    position: relative
    font-size: 11px
    -webkit-transform: translateZ(0)
    -ms-transform: translateZ(0)
    transform: translateZ(0)
    -webkit-animation-delay: -0.16s
    animation-delay: -0.16s

  .loader:after
    left: 1.5em

  @-webkit-keyframes load1
    0%,
    80%,
    100%
      box-shadow: 0 0
      height: 4em
    40%
      box-shadow: 0 -2em
      height: 5em

  @keyframes load1
    0%,
    80%,
    100%
      box-shadow: 0 0
      height: 4em
    40%
      box-shadow: 0 -2em
      height: 5em
</style>

<script>
  import Vue from 'vue'

  export default{
    name: 'Drops',

    data () {
      return {
        content: '<div class="loader">Loading...</div>',
        imgRoot: 'https://superkieran.github.io/wooyun-img/'
      }
    },

    created () {
      this.getDrop()
    },

    watch: {
      '$route.params.title': function () {
        this.content = '<div class="loader">Loading...</div>'
        this.getDrop()
      },
      'content': function () {
        Vue.nextTick(() => {
          let imgs = document.getElementsByTagName('img')
          let len = imgs.length
          for (let i = 0; i < len; i++) {
            if (imgs[i] && imgs[i].getAttribute('img-src')) {
              imgs[i].parentNode.innerHTML = '<a href="' + this.imgRoot +
                imgs[i].getAttribute('img-src') + '" data-lightbox="image-' + i + '">' +
                '<img src="' + this.imgRoot + imgs[i].getAttribute('img-src') + '" />' + '</a>'
              /* imgs[i].setAttribute('src', this.imgRoot + imgs[i].getAttribute('img-src')) */
            }
          }
        })
      }
    },

    methods: {
      getDrop () {
        if (this.$route.params.title) {
          let _content = localStorage.getItem(this.$route.params.title)
          if (!_content) {
            this.$http.get(`static/drops/${this.$route.params.title}.html`)
              .then(res => {
                this.content = res.body
                localStorage.setItem(this.$route.params.title, res.body)
              })
          } else {
            this.content = _content
          }
        }
      }
    }
  }
</script>



